* {
  box-sizing: border-box;
}
h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}
body {
  margin: 0;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 550px;
  align-items: center;
}

/* NAV styles */

nav {
  display: flex;
  align-items: center;
  background-color: #ffffff;
  padding: 30px 25px;
  height: 60px;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.25);
  z-index: 1;
  width: 550px;
}
.nav-item{
  width: 29px;
}
nav.dark {
  background-color: #21222A;
}
.nav--logo_icon {
  width: 30px;
  margin-right: 10px;
}
.nav--logo_text {
  color: #61DAFB;
  margin-right: auto;
}
.nav--title {
  color: #DEEBF8;
}
.toggler {
  display: flex;
  align-items: center;
}
.toggler p {
  font-size: 0.625rem;
  font-weight: 600;
}
.toggler--slider {
  width: 25px;
  height: 13px;
  background-color: #2B283A;
  border-radius: 2rem;
  margin-inline: 7px;
  cursor: pointer;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  border: 1px solid black;
  box-sizing: content-box;
}
nav.dark .toggler--slider {
  border-color: white;
  background-color: #F5F5F5;
}
.toggler--slider--circle {
  height: 13px;
  width: 13px;
  background-color: white;
  border-radius: 50%;
}
nav.dark .toggler--slider--circle {
  background-color: black;
}
.toggler--light {
  color: #2B283A;
}
nav.dark .toggler--light {
  color: #918E9B;
}
.toggler--dark {
  color: #D5D4D8;
}
nav.dark .toggler-dark {
  color: #FFFFFF;
}
nav.dark .toggler--slider {
  justify-content: flex-end;
}

/* MAIN styles */
main {
  padding: 57px 27px;
  background-image: url("./image/reactjs-icon\ 2.png"); 
  background-repeat: no-repeat;
  background-position: right center;
  flex: 1 1 auto;
  background-color: white;
  color: #2B283A;
  width: 550px;
  height: 50%;  
}

main.dark {
  background-image: url("./image/reactjs-icon\ 2.png"); 
  background-color: #282D35;
  color: white;  
  
}

.main--title {
  margin: 0;
  font-size: 39px;
  letter-spacing: -0.05em;
}

.main--facts {
  margin-top: 46px;
  max-width: 400px;
}

.main--facts > li {
  line-height: 19px;
  padding-block: 10px;
}

.main--facts > li::marker {
  font-size: 1.4rem;
  color: #61DAFB;
}
